import React, { useState, useEffect } from 'react';
import Slider from 'react-slick';
import styled from 'styled-components';
import 'slick-carousel/slick/slick.css';
import 'slick-carousel/slick/slick-theme.css';

const Container = styled.div`
  position: relative;
  margin-top: 10px;
  width: 100%;
  flex-grow: 3;
  .slick-slider {
    position: relative;
  }
`;

const Image = styled.img`
  width: 100%;
`;

const ImageLrgContainer = styled.div`
  position: fixed;
  top: 0;
  left: 0;
  height: 100vh;
  width: 100vw;
  background-color: rgba(0, 0, 0, 0.3);
  z-index: 99;
`;

const ImageLrg = styled.img`
  position: fixed;
  width: 70vw;
  top: 50vh;
  left: 0;
  transform: translate(20%, -50%);
  z-index: 99;
  @media (max-width: 768px) {
    width: 100vw;
    transform: translateY(-50%);
  }
`;

interface Props {
  images: string[];
}

const Carousel: React.FC<Props> = ({ images }) => {
  const [showImageLrg, setShowImageLrg] = useState(false);
  useEffect(() => {
    showImageLrg
      ? (document.body.style.overflow = 'hidden')
      : (document.body.style.overflow = 'unset');
  }, [showImageLrg]);

  const [selectedImage, setSelectedImage] = useState<string>('');
  const settings = {
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,
    autoplay: true,
    autplaySpeed: 2000,
    pauseOnHover: true,
  };

  const setImage = (index: number) => {
    images.forEach((image, i) => {
      if (i === index) setSelectedImage(image);
    });
  };

  return (
    <Container>
      <Slider {...settings}>
        {images.map((image, i) => {
          return (
            <div key={i}>
              <Image
                src={image}
                alt='project image'
                onClick={() => {
                  setImage(i);
                  setShowImageLrg(!showImageLrg);
                }}
              />
            </div>
          );
        })}
      </Slider>
      {showImageLrg && (
        <ImageLrgContainer
          onClick={() => {
            setShowImageLrg(!showImageLrg);
          }}
        >
          <ImageLrg src={selectedImage} alt='project image'></ImageLrg>
        </ImageLrgContainer>
      )}
    </Container>
  );
};

export default Carousel;
