import React from 'react';
import styled, { keyframes, css } from 'styled-components';

const LogoTooltip = styled.div`
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  text-align: center;
  padding: 0px 5px 2px 5px;
  border-radius: 10px;
  background-color: rgba(0, 0, 0, 0.6);
  color: white;
  z-index: 999;
  display: none;
`;

const LogoWrapper = styled.div`
  position: relative;
  &:hover ${LogoTooltip} {
    display: block;
  }
`;

const SkillLogo = styled.img`
  width: 40px;
  height: 40px;
  @media (max-width: 1024px) {
    width: 30px;
    height: 30px;
  }
`;

interface Props {
  logo: string;
  tooltip: string;
}

const SkillsLogo: React.FC<Props> = ({ logo, tooltip }) => {
  return (
    <LogoWrapper>
      <SkillLogo src={logo} alt={tooltip} />
      <LogoTooltip>{tooltip}</LogoTooltip>
    </LogoWrapper>
  );
};

export default SkillsLogo;
